import React, { Component } from 'react';
import { Grid, Row, Col, Modal } from "react-bootstrap";
// import SweetAlert from 'react-bootstrap-sweetalert';
import swal from 'sweetalert';
import config from '../constant/config.js';
const axios = require('axios');

let intervalSet;
let timerWaitInterval;
let timerWait = 100;
class Innerlogin extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loginForm: false,
            setShow: false,
            show: true,
            login: '',
            password: '',
            broker: '',
            alias: '',
            alert: null,
            persons: [],
            shwPwd: false,
            shwPwd: false,
            shwLogin: false,
            shwbroker: false,
            accountlist: [],
            editForm: false,
            detailForm: false,
            editData: [],
            edituser: '',
            editserver: '',
            editalias: '',
            editstatus: '',
            id: '',
            // tryAgainConnection: false,
            currentAddLogin: null,
            currentAddBroker: null,
            currentAddAlias: null,
            timerShow: false,
            closebtn: true
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.onUpdate = this.onUpdate.bind(this);

    }
    // delete alert
    // deleteThisGoal() {
    //     const getAlert = () => (
    //         <SweetAlert
    //             success
    //             title="Woot!"
    //             onConfirm={() => this.hideAlert()}
    //         >
    //             Hello world!
    //       </SweetAlert>
    //     );

    //     this.setState({
    //         alert: getAlert()
    //     });
    // }
    fectheAllAccount() {
        let self = this;
        axios.get(config.baseurl + '/users/allAccounts?page=1')
            .then(function (response) {
                if (response.status == 200) {
                    self.setState({ accountlist: response.data.rows })
                }
            })
        // console.log(response.status,"accountlist");
    }
    onChange(e) {

        this.setState({ error: '' })
        if (e.target.name === 'login') {
            const re = /^[0-9\b]+$/;
            if (e.target.value === '' || re.test(e.target.value)) {
                this.setState({ [e.target.name]: e.target.value })
            }
            else if (e.target.value !== re) {
                this.setState({ error: 'alphabet' })
            }
        }
        else {

            this.setState({ [e.target.name]: e.target.value.trim() })
        }

    }

    checkUserConnected = (login, broker) => {
        let self = this;
        axios.post(config.baseurl + '/users/checkUserConnected', { login: login, broker: broker })
            .then(function (response) {

                if (response.status == 200) {
                    if (response.data.connected) {
                        self.setState({ currentAddLogin: null, currentAddBroker: null, currentAddAlias: null });
                        clearInterval(intervalSet);
                        clearInterval(timerWaitInterval);
                        self.setState({ timerShow: false });

                        timerWait = 100
                        // self.setState({ tryAgainConnection: false });
                        self.setState({ loginForm: false })
                        self.handleClose();
                        self.fectheAllAccount()
                        // swal({
                        //     text: "Connected",
                        //     timer: 2000,
                        //     button: false
                        // })
                    }
                } else {

                }
            })
    }

    setIntervalCustom = () => {
        let self = this;
        var name = "Authorization Process Failed";
        var content = document.createElement('div');
        content.innerHTML = ' <strong>' + name + '</strong>';


        timerWaitInterval = setInterval(function () {
            timerWait--;
            if (timerWait <= 0) {
                self.setState({ currentAddLogin: null, currentAddBroker: null, currentAddAlias: null });
                clearInterval(intervalSet);
                clearInterval(timerWaitInterval);
                self.setState({ timerShow: false });
                self.setState({ loginForm: false })
                self.setState({ closebtn: true })
                timerWait = 100
                swal({


                    content: content,
                    timer: 2000,
                    button: false

                })
            }

            else {
                self.setState({ timerShow: true });

            }
        }, 1000);
    }


    onSubmit(e) {
        e.preventDefault();
        const userdetail = {
            login: this.state.login,
            password: this.state.password,
            broker: this.state.broker,
            alias: this.state.alias


        }
        if (this.state.login == '') {
            this.setState({ error: 'login' })
        }
        else if (this.state.password == '') {
            this.setState({ error: 'password' })
        }
        else if (this.state.broker == '') {
            this.setState({ error: 'broker' })
        } else {
            this.setState({ error: '' })
        }
        if (this.state.login !== '' && this.state.password !== '' && this.state.broker !== '') {

            // {
            //     swal({
            //         text: "User Already Connected",
            //         timer: 2000,
            //         button: false
            //     })
            // }
            let self = this;
            self.setState({ closebtn: false })
            axios.post(config.baseurl + '/users/addUser', userdetail)
                .then(function (response) {
                    if (response.data.status === true) {
                        self.setIntervalCustom();
                        self.fectheAllAccount();
                        self.setState({ currentAddLogin: userdetail.login, currentAddBroker: userdetail.broker, currentAddAlias: userdetail.alias });
                        intervalSet = setInterval(function () {
                            self.checkUserConnected(userdetail.login, userdetail.broker)
                        }, 5000);

                    }
                    if (response.data.status == 202) {
                        swal({
                            text: "User doesn't exist",
                            timer: 2000,
                            button: false
                        })

                    }
                    if (response.data.status == '500') {
                        swal({
                            text: "User Already Connected!",
                            timer: 2000,
                            button: false
                        })
                        self.setState({ closebtn: true })


                    }
                    else if (response.status == 201) {
                        swal({
                            title: "Done!",
                            text: response.data,
                            // icon: "{response.status == 200 ? 'warning ':'success'}",
                            timer: 2000,
                            button: false
                        })
                    }

                })
                .catch(function (error) {
                    self.handleClose();
                });



            /////////////////// redirect to dashboard
            // login(user).then(res => {
            //     if (res) {
            //         this.props.history.push(`\profile`)
            //     }
            // })
        }
    }
    onUpdate(e) {
        e.preventDefault();
        const userdetail = {
            id: this.state.id,
            user: this.state.edituser,
            broker: this.state.editserver,
            alias: this.state.editalias
        }
        let self = this;
        axios.post(config.baseurl + '/users/updateuser', userdetail)
            .then(function (response) {
                if (response.status == 200) {
                    // self.setState({ accountlist: response.data.rows })
                    self.fectheAllAccount();
                    self.handleCloseEdit();
                }
            })
            .catch(function (error) {
            });
    }
    // checkValidUser = ()=> {
    //     console.log(this.state,'loginnnnnnnnnnnnnnnnnnnnnnnn')
    //     const userdetail = {
    //         login: this.state.login,
    //         password: this.state.password,
    //         broker: this.state.broker,
    //         alias: this.state.alias
    //     }
    //     axios.get(config.baseurl + '/accounts/matchUser',userdetail)
    //     .then(function (response) {

    //         if (response.status == 200) {
    //             // alert(response.data)
    //             swal({
    //                 title: "Done!",
    //                 text: response.data,
    //                 // icon: "{response.status == 200 ? 'warning ':'success'}",
    //                 timer: 2000,
    //                 button: false
    //             })

    //         }
    //         // console.log(response);
    //     })
    //     .catch(function (error) {
    //         console.log(error);
    //     });
    // }
    toogle = () => {
        this.setState({ loginForm: !this.state.loginForm, })
        this.setState({ login: '', password: '', broker: '', alias: '' })
    }

    handleClose = () => {
        this.setState({ loginForm: false })

    }
    handleCloseEdit = () => {
        this.setState({ editForm: false })
    }
    handleCloseDetail = () => {
        this.setState({ detailForm: false })
    }
    handleShow = () => {
        this.setState({ setShow: true })
    }

    componentDidMount() {
        this.fectheAllAccount();

    }
    createTr = () => {
        return this.state.accountlist.length > 0 && this.state.accountlist.map((item, index) => {
            return (
                <tr key={index}>
                    <th scope="row">{index + 1}</th>
                    <td>{item.login}</td>
                    <td>{item.broker}</td>
                    <td>{item.alias}</td>
                    <td style={{ fontWeight: 600 }}>{item.launched == 1 ? 'Connected' : 'Disconnected'}</td>
                    <td>
                        <a href="" onClick={(e) => this.viewFilter(e, item)}><i className="fa fa-eye facolor" aria-hidden="true"></i></a>
                        <a href="" onClick={(e) => this.editFilter(e, item)}><i className="fa fa-pencil-square-o facolor" aria-hidden="true"></i></a>
                        {/* <a href="" onClick={(e)=> this.deleteFilter(e,item)}><i className="fa fa-trash" aria-hidden="true"></i></a> */}
                    </td>
                </tr>
            )
        })
    }
    editFilter(e, data) {
        e.preventDefault();
        this.setState({ editForm: true, editData: data, id: data.id, edituser: data.login, editserver: data.broker, editalias: data.alias })
    }
    editAlias(e) {
        this.setState({ editalias: e.target.value })
    }
    viewFilter(e, data) {
        e.preventDefault();
        this.setState({ detailForm: true, edituser: data.login, editserver: data.broker, editalias: data.alias, editstatus: data.status })
    }
    render() {

        return (
            <div className="content">
                <Grid fluid>
                    <Row>
                        <Col md={12} sm={6}>
                            <div className="terminal-box">
                                <span className="terminal-text">Terminal Accounts</span>
                                {/* <div style={{ padding: '20px' }}>
                                    <a
                                        onClick={() => this.deleteThisGoal()}
                                        className='btn btn-danger'
                                    >
                                        <i className="fa fa-trash" aria-hidden="true"></i> Delete Goal
                                     </a>
                                    {this.state.alert}
                                </div> */}
                            </div>
                            <button className="pull-right add-button" onClick={this.toogle}>Add New</button>

                            {this.state.loginForm &&
                                <Modal show={this.state.show} onHide={this.state.closebtn ? this.handleClose : () => console.log('error')} >
                                    {/* <Modal.Header closeButton>
                                        <Modal.Title>Log In</Modal.Title>
                                    </Modal.Header> */}
                                    <Modal.Body>
                                        <div className="form-box">
                                            <Modal.Header closeButton={this.state.closebtn ? true : false}>
                                                <Modal.Title className="text-center"><b>Log In</b></Modal.Title>
                                            </Modal.Header>
                                            <form style={{ marginTop: '20px' }} onSubmit={this.onSubmit}>
                                                <label >User Name</label>
                                                <input type='text'
                                                    className="Itext labelSet " placeholder="Enter Username" name="login" value={this.state.login} onChange={this.onChange} disabled={this.state.currentAddLogin ? true : false}
                                                // onKeyUp={this.checkValidUser}
                                                ></input>


                                                {(this.state.error == "login") &&
                                                    <span style={{ color: 'red', fontSize: '13px' }}>Please enter Username </span>
                                                }

                                                {(this.state.error == "alphabet") &&
                                                    <span style={{ color: 'red', fontSize: '13px' }}>"Invalid format. login should contain only digits."</span>
                                                }
                                                <br></br>
                                                <label className="labeAdjust">Password</label>
                                                <input className="Itext labelSet" placeholder="Password" name="password" type="password" value={this.state.password} onChange={this.onChange} disabled={this.state.currentAddLogin ? true : false}></input>
                                                {(this.state.error == "password") &&
                                                    <span style={{ color: 'red', fontSize: '13px' }}>Please enter Password </span>
                                                }
                                                <br></br>
                                                <label className="labeAdjust">Server</label>
                                                <input className="Itext labelSet" name="broker" placeholder="Server" value={this.state.broker} onChange={this.onChange} disabled={this.state.currentAddBroker ? true : false}></input>
                                                {(this.state.error == "broker") &&
                                                    <span style={{ color: 'red', fontSize: '13px' }}>Please enter Server </span>
                                                }
                                                <br></br>
                                                <label className="labeAdjust">Alias</label>
                                                <input className="Itext labelSet" placeholder="Alias" value={this.state.alias} onChange={this.onChange} name="alias" disabled={this.state.currentAddAlias ? true : false}></input>
                                                {this.state.timerShow &&
                                                    <p>
                                                        Connecting to server... {timerWait} seconds remains
                                                </p>
                                                }
                                                <div className=" topadjust clearfix">
                                                    {/* {this.state.tryAgainConnection === false ? */}
                                                    {/* ( */}
                                                    {this.state.timerShow == false &&

                                                        <button type="submit" className="save" className={!this.state.login || !this.state.password || !this.state.broker ? "loginButton" : "loginButton save2 save1"}>Submit</button>}
                                                    {/* ) */}
                                                    {/* //     :
                                                    //     (this.state.timerShow == false && <div ></div>)
                                                    // } */}
                                                </div>
                                            </form>
                                        </div>

                                    </Modal.Body>
                                    {/* <Modal.Footer>
                                        <Button variant="secondary" onClick={this.handleClose}>
                                            Close
                                  </Button>
                                        
                                    </Modal.Footer> */}
                                </Modal>

                            }
                        </Col>
                    </Row>
                    <div style={{ background: '#ffffff' }}>
                        <Row>
                            <Col md={12}>
                                <div style={{ padding: '10px' }}>
                                    <table className="table table-striped">
                                        <thead>
                                            <tr>
                                                <th scope="col">Sr.No.</th>
                                                <th scope="col">User Name</th>
                                                <th scope="col">Server</th>
                                                <th scope="col">Alias</th>
                                                <th scope="col">Status</th>
                                                <th scope="col">Action</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {/* {this.state.accountlist.length > 0 && this.state.accountlist.map((item, index) => { */}
                                            {/* // console.log("hhhhhhhhhhh") */}
                                            {this.createTr()}
                                            {/* })} */}
                                            {/* <tr>
                                                <th scope="row">2</th>
                                                <td>Jacob</td>
                                                <td>Thornton</td>
                                                <td>5.1.2.9</td>
                                                <td>Connected</td>
                                            </tr>
                                            <tr>
                                                <th scope="row">3</th>
                                                <td>Larry</td>
                                                <td>the Bird</td>
                                                <td>ab@123</td>
                                                <td>Connected</td>
                                            </tr> */}
                                        </tbody>
                                    </table>
                                </div>
                            </Col>
                            <div className="centerbtn">
                            </div>

                        </Row>
                    </div>

                </Grid>

                {this.state.editForm &&
                    <Modal show={this.state.show} onHide={this.handleCloseEdit}>
                        {/* <Modal.Header closeButton>
                                        <Modal.Title>Log In</Modal.Title>
                                    </Modal.Header> */}
                        <Modal.Body>
                            <div className="form-box">
                                <Modal.Header closeButton>
                                    <Modal.Title className="text-center"><b>Edit</b></Modal.Title>
                                </Modal.Header>
                                <form style={{ marginTop: '20px' }} onSubmit={this.onUpdate}>
                                    <label>USER</label>
                                    <input className="Itext" disabled name="broker" placeholder="Server" value={this.state.edituser} ></input>

                                    <label>Server</label>
                                    <input className="Itext" disabled name="broker" placeholder="Server" value={this.state.editserver} ></input>
                                    <label>Alias</label>
                                    <input className="Itext" placeholder="Alias" value={this.state.editalias} name="alias" onChange={(e) => this.editAlias(e)}></input>
                                    <div className="clearfix">
                                        <button type="submit" className="save">Update</button>
                                    </div>
                                </form>
                            </div>

                        </Modal.Body>
                        {/* <Modal.Footer>
                                        <Button variant="secondary" onClick={this.handleClose}>
                                            Close
                                  </Button>
                                        
                                    </Modal.Footer> */}
                    </Modal>

                }

                {this.state.detailForm &&
                    <Modal show={this.state.show} onHide={this.handleCloseDetail}>
                        {/* <Modal.Header closeButton>
                                        <Modal.Title>Log In</Modal.Title>
                                    </Modal.Header> */}
                        <Modal.Body>
                            <div className="form-box">
                                <Modal.Header closeButton>
                                    <Modal.Title className="text-center"><b>Details</b></Modal.Title>
                                </Modal.Header>
                                <div className="detail_div">
                                    <table>
                                        <tr>
                                            <td><label>User Name</label></td>
                                            <td><label>{this.state.edituser}</label></td>
                                        </tr>
                                        <tr>
                                            <td><label>Server</label></td>
                                            <td><label>{this.state.editserver}</label></td>
                                        </tr>
                                        <tr>
                                            <td><label>Alias</label></td>
                                            <td><label>{this.state.editalias}</label></td>
                                        </tr>
                                        <tr>
                                            <td><label>Status</label></td>
                                            <td><label>{this.state.editstatus == 1 ? 'Connected' : 'Disconnected'}</label></td>
                                        </tr>
                                    </table>
                                </div>
                            </div>

                        </Modal.Body>
                    </Modal>

                }


            </div >
        );
    }
}

export default Innerlogin;