import React, { Component } from 'react'
import NumberFormat from 'react-number-format';
import config from '../../constant/config';
import Data from '../Data';
import { Grid, Row, Col } from "react-bootstrap";
import './ContractSize.css';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"
import Loader from 'react-loader-spinner'
const axios = require('axios');
export default class ContractSize extends Component {
    constructor(props) {
        super(props)
        this.state = {
            fromAccounts: '',
            fromSymbol: '',
            toAccounts: '',
            toSymbol: '',
            fromOpenOrderInfos: '',
            toOpenOrderInfos: '',
            total: '',
            totalTo: '',
            blockloader: true,
            selectedOption: null,
            allHistoryRecord: [],
            allOpenRecord: [],
            fromHistoryOrderInfo: [],
            toHistoryOrderInfo: [],
            commissionHistoryOrderInfo: [],
            fromOpenOrderInfo: [],
            toOpenOrderInfo: [],
            filterlist: [],
            loading: true,
            dateTime: '',
            errorMassege: true,



        }
    }

    componentDidMount() {
        this.fetchAllHIstoryTrade();
        this.fetchStatusData();
        axios.get(config.baseurl + '/accounts/fetchAllSymbol')
            .then(function (response) {
                if (response.status == 200) {
                    console.log(response, "response ---------------fetchAllSymbol>")
                }
            })
        this.fetchLastUpdatedTime();
    }
    fetchLastUpdatedTime = () => {
        let self = this;
        axios.get(config.baseurl + '/accounts/fetchLastUpdatedTime')
            .then(function (response) {
                if (response.status == 200) {
                    self.setState({ dateTime: response.data.filteredInfoTime })
                    // console.log(self.state.dataOpenTrade.accountFromInfo,"outyfjhkjh");
                }
            });
    }

    fetchStatusData = () => {
        let self = this;
        let tottal = 0;
        axios.get(config.baseurl + '/accounts/fetchStatusData')
            .then(function (response) {
                if (response.status == 200) {
                    self.setState({ loading: false })

                    self.setState({ fromAccounts: response.data.fromAccounts })
                    self.setState({ fromSymbol: response.data.fromSymbol })
                    // console.log(fromSymbol,"fromSymbol");
                    self.setState({ toAccounts: response.data.toAccounts })
                    self.setState({ toSymbol: response.data.toSymbol })
                    self.setState({ fromOpenOrderInfos: response.data.fromOpenOrderInfos.length > 0 ? response.data.fromOpenOrderInfos[0] : '' })
                    if (response.data.fromOpenOrderInfos.length > 0) {
                        self.setState({ blockloader: false })
                        if (response.data.fromOpenOrderInfos[0].order_type == 0) {
                            self.setState({ total: response.data.fromSymbol.contract_size * (response.data.fromOpenOrderInfos[0].lots) })
                        } else {
                            self.setState({ total: response.data.fromSymbol.contract_size * -(response.data.fromOpenOrderInfos[0].lots) })
                        }

                        self.setState({ toOpenOrderInfos: response.data.toOpenOrderInfos.length > 0 ? response.data.toOpenOrderInfos[0] : '' })
                    }
                    if (response.data.toOpenOrderInfos.length > 0) {
                        if (response.data.toOpenOrderInfos[0].order_type == 0) {
                            self.setState({ totalTo: response.data.toSymbol.contract_size * (response.data.toOpenOrderInfos[0].lots) })
                        } else {
                            self.setState({ totalTo: response.data.toSymbol.contract_size * -(response.data.toOpenOrderInfos[0].lots) })
                        }
                    }

                }
            });
    }
    fetchAllHIstoryTrade = () => {
        let self = this;
        axios.get(config.baseurl + '/accounts/fetchAllHistoryTrade')
            .then(function (response) {
                // console.log(response.data, 'response.data his')
                if (response.status == 200) {
                    self.setState({ errorMassege: false })
                    // self.setState({ allHistoryRecord: response.data.rows })
                    self.setState({
                        allHistoryRecord: response.data.rows,
                        fromHistoryOrderInfo: response.data.fromHistoryOrderInfo,
                        toHistoryOrderInfo: response.data.toHistoryOrderInfo,
                        commissionHistoryOrderInfo: response.data.commissionHistoryOrderInfo
                    })
                }
            })
        // axios.post(config.baseurl + '/accounts/fetchAllBySymbolOpen', {'symbol':symbol})
        // axios.post(config.baseurl + '/accounts/fetchAllBySymbolOpen', postData)
        axios.get(config.baseurl + '/accounts/fetchAllOpenTrade')
            .then(function (response) {
                if (response.status == 200) {

                    // self.setState({ allOpenRecord: response.data.rows })
                    self.setState({
                        allOpenRecord: response.data.rows,
                        fromOpenOrderInfo: response.data.fromOpenOrderInfo,
                        toOpenOrderInfo: response.data.toOpenOrderInfo
                    })
                }
            })
    }

    render() {

        const { allHistoryRecord, allOpenRecord, selectedOption, toOpenOrderInfo, fromOpenOrderInfo, fromHistoryOrderInfo, commissionHistoryOrderInfo, toHistoryOrderInfo } = this.state;
        //   const sumOfOpenTotal = allOpenRecord.reduce((current, data) => {
        //     return (Math.floor(data.commission + data.swap + data.profit+ current));
        // }, 0);

        //   const sumOfHistoryTotal = allHistoryRecord.reduce((current, data) => {
        //     return (data.profit + current);
        // }, 0);
        let uniqueSymbols = "";
        if (allOpenRecord && Object.entries(allOpenRecord).length > 0) {
            let fromSymbols = JSON.parse(allOpenRecord.from_symbols);
            // fromSymbols = (fromSymbols).map(data => data).join(', ');
            let toSymbols = JSON.parse(allOpenRecord.to_symbols);
            let combineSymbols = fromSymbols.concat(toSymbols);
            uniqueSymbols = combineSymbols.filter((item, i, ar) => ar.indexOf(item) === i);
            uniqueSymbols = (uniqueSymbols).map(data => data).join(', ')
        }
        let fromsum = 0;
        let tosum = 0;
        // if(fromHistoryOrderInfo && commissionHistoryOrderInfo && fromHistoryOrderInfo.length>0){
        if (fromHistoryOrderInfo && fromHistoryOrderInfo.length > 0) {
            fromsum = (fromHistoryOrderInfo[0].commission + fromHistoryOrderInfo[0].swap + fromHistoryOrderInfo[0].profit);
        }

        if (toHistoryOrderInfo && toHistoryOrderInfo.length > 0) {
            tosum = (toHistoryOrderInfo[0].commission + toHistoryOrderInfo[0].swap + toHistoryOrderInfo[0].profit);
        }

        let sumOfHistoryTotal = tosum + fromsum;

        let openfromsum = 0;
        let opentosum = 0;
        if ((fromOpenOrderInfo && fromOpenOrderInfo.length > 0 && fromOpenOrderInfo[0].total != null)) {

            openfromsum = (fromOpenOrderInfo[0].commission + fromOpenOrderInfo[0].swap + fromOpenOrderInfo[0].profit);

        }
        if ((toOpenOrderInfo && toOpenOrderInfo.length > 0 && toOpenOrderInfo[0].total != null)) {

            opentosum = (toOpenOrderInfo[0].commission + toOpenOrderInfo[0].swap + toOpenOrderInfo[0].profit);
        }

        let sumOfOpenTotal = opentosum + openfromsum;



        return (
            <React.Fragment>
                {this.state.errorMassege == true ?
                    <div className="popup">

                        <div className="card w-75 ">
                            <div className="card-body topmargin">
                                <div className=" card-text popuptext" role="alert">Please Select Filters To Continue...</div>
                            </div>
                        </div>
                    </div> :
                    <>
                        <Row>
                            <div className="lastUpdate">
                                <span> Last Updated :  </span>
                                {this.state.dateTime}(UTC)  </div>
                        </Row>
                        <div className="contractSize-wrapper">
                            <div className=" leftset card ">
                                {this.state.loading ? <Loader
                                    type="ThreeDots"
                                    color="black"
                                    height={100}
                                    width={100}
                                    style={{ textAlign: 'center' }}


                                />
                                    :
                                    <Row>

                                        <h4 className="text-center "><b>Exposure</b></h4>
                                        {/* {this.state.fromOpenOrderInfos && this.state.fromOpenOrderInfos.order_type !== null || this.state.toOpenOrderInfos && this.state.toOpenOrderInfos.order_type !== null ? */}
                                        <>   {this.state.fromOpenOrderInfos !== '' && this.state.fromOpenOrderInfos.order_type !== null ?
                                            <Col md={4}>
                                                <div className="card">
                                                    <div className={'colorDarkBlue , boldText loginCenter'}>{this.state.fromAccounts.alias}</div>

                                                    <h6 className={'colorDarkBlue loginCenter'}>{this.state.fromAccounts.login}</h6>

                                                    <div className="content table-full-width "><div className="table-responsive">
                                                        <div className="text-center">
                                                            <span >{this.state.fromSymbol.name}

                                                            </span>
                                                            {(this.state.fromOpenOrderInfos !== '' && this.state.fromOpenOrderInfos.order_type !== null) && <span className="td-actions bText, clearBorder" style={{ display: 'block' }}>
                                                                {this.state.fromOpenOrderInfos.order_type == 0 ? 'BUY ' : 'SELL '}

                                                                {this.state.fromOpenOrderInfos.lots} {(this.state.fromOpenOrderInfos.lots > 1) ? 'Lots' : 'Lot'}
                                                            </span>}
                                                        </div><br></br>
                                                        <div className="text-center">
                                                            <span className="td-actions clearBorder text-right">
                                                                <NumberFormat value={this.state.total} displayType={'text'} thousandSeparator={true} decimalScale={2} />   {this.state.fromSymbol.margin_currency}

                                                            </span>
                                                        </div>

                                                    </div>
                                                    </div>
                                                </div>

                                            </Col>
                                            :
                                            <Col md={4}>
                                                <div className="card change noRecordCol"> No Record Found</div>
                                            </Col>
                                        }
                                            <Col md={4}>
                                                <div className="card">

                                                    <h5 className="  text-center adjustMent" > {this.state.total != 0 && this.state.totalTo != 0 && (this.state.total + this.state.totalTo != 0) ? <b>  Exposure</b> : <span style={{ color: "green" }}><b > No Exposure</b></span>}
                                                    </h5>
                                                    {this.state.total != 0 && this.state.totalTo != 0 && (this.state.total + this.state.totalTo != 0) &&
                                                        <div style={{ color: '#b70606' }} className=" setBottom bText text-center">
                                                            <NumberFormat
                                                                value={this.state.total + this.state.totalTo} displayType={'text'} thousandSeparator={true} decimalScale={2} />
                                                        </div>

                                                    }
                                                </div>

                                            </Col>
                                            {this.state.toOpenOrderInfos !== '' && this.state.toOpenOrderInfos.order_type !== null ? <Col md={4}>
                                                <div className=" leftset1 card">
                                                    <div className={'color2 , boldText loginCenter'}>{this.state.toAccounts.alias}</div>

                                                    <h6 className={'color2 ,bText, loginCenter'}>{this.state.toAccounts.login}</h6>

                                                    <div className="content table-full-width "><div className="table-responsive">


                                                        <div className="text-center">
                                                            <span className="td-actions bText, clearBorder text-right">{this.state.toSymbol.name}

                                                            </span>
                                                            {(this.state.toOpenOrderInfos !== '' && this.state.toOpenOrderInfos.order_type !== null) && <span className="td-actions bText, clearBorder" style={{ display: 'block' }}>
                                                                {this.state.toOpenOrderInfos.order_type == 0 ? 'BUY ' : 'SELL '}
                                                                {this.state.toOpenOrderInfos.lots} {(this.state.toOpenOrderInfos.lots > 1) ? 'Lots' : 'Lot'}</span>}
                                                        </div><br></br>
                                                        <div className="text-center">
                                                            <span >
                                                                <NumberFormat value={this.state.totalTo} displayType={'text'} thousandSeparator={true} decimalScale={2} />   {this.state.toSymbol.margin_currency}

                                                            </span>
                                                        </div>

                                                    </div>
                                                    </div>
                                                </div>

                                            </Col>
                                                :
                                                <Col md={4}>
                                                    <div className="card change noRecordCol"> No Record Found</div>
                                                </Col>}

                                        </>
                                        {/* <Col md={12}>
                                            <div className="card change noRecordCol"> No Recor Found</div>
                                        </Col> */}
                                        {/* } */}

                                    </Row>}
                                <Col md={12} sm={6}>
                                    <div className={sumOfHistoryTotal + sumOfOpenTotal >= 0 ? "summary-box" : "summary-box-red"}>


                                        {/* <span style={{fontWeight:'bold'}}>+150 USD</span> */}
                                        {/* <p className="summary-text" style={{fontWeight:600}}>Summary For {(selectedOption!==null) && selectedOption.value} (open + closed) </p>
                <span style={{fontWeight:'bold'}}>{sumOfHistoryTotal + sumOfOpenTotal} $</span> */}

                                        <Col md={12} sm={12}>
                                            <div className="summary-text text-center" style={{ fontWeight: 600, color: "#797979" }}>Summary For ({uniqueSymbols}) {allHistoryRecord.history_info !== 0 && (<span> + Commission</span>)} </div>
                                        </Col>
                                        <Col md={12} sm={12}>
                                            <div className="text-center" style={{ fontWeight: 'bold', fontSize: "18px" }}>$ {' '}
                                                <NumberFormat value={allHistoryRecord.history_info !== undefined ? sumOfHistoryTotal + sumOfOpenTotal + allHistoryRecord.history_info : sumOfHistoryTotal + sumOfOpenTotal} displayType={'text'} thousandSeparator={true} decimalScale={2} />

                                            </div>
                                        </Col>

                                        <div className="clearfix"></div>
                                    </div>
                                </Col>
                            </div>
                        </div></>}
            </React.Fragment>
        )
    }
}
