import React, { Component } from "react";
// import ChartistGraph from "react-chartist";
import { Grid, Row, Col } from "react-bootstrap";
import TableList from '../views/TableList';
// import { Card } from "components/Card/Card.jsx";
// import { StatsCard } from "components/StatsCard/StatsCard.jsx";
// import { Tasks } from "components/Tasks/Tasks.jsx";
import NumberFormat from 'react-number-format';
import ReactLoading from "react-loading";
import Loader from 'react-loader-spinner'

import Data from './Data';
import Moment from 'react-moment';
import config from '../constant/config.js';
// import {DatatablePage} from './DatatablePage';
const axios = require('axios');
class Dashboard extends Component {
  constructor(props) {
    super(props)
    this.state = {
      details: [],
      accountlist: [],
      filterlist: [],
      errorMassege: false,
      error: '',
      dateTime: '',
      fromAccounts: '',
      fromSymbol: '',
      toAccounts: '',
      toSymbol: '',
      fromOpenOrderInfos: '',
      toOpenOrderInfos: '',
      total: '',
      totalTo: '',
      blockloader: true,
      activeProfileName: '',
      autoClose: ' ',
      //


      allHistoryRecord: [],
      allOpenRecord: [],
      fromHistoryOrderInfo: [],
      toHistoryOrderInfo: [],
      commissionHistoryOrderInfo: [],
      fromOpenOrderInfo: [],
      toOpenOrderInfo: [],
      loading: true,
      //


    }
  }
  componentWillMount() {
    this.fetchCloseAndOpenPositionTotal();
    this.getAllAccounts();
    this.fetchActivefilterData();
    this.fetchAllOpenTrade();
    this.fetchStatusData();


  }
  fetchCloseAndOpenPositionTotal = () => {
    let self = this;
    axios.get(config.baseurl + '/accounts/fetchAllHistoryTrade')
      .then(function (response) {
        console.log(response.data, 'response.data his')
        if (response.status == 200) {
          self.setState({
            allHistoryRecord: response.data.rows,
            fromHistoryOrderInfo: response.data.fromHistoryOrderInfo,
            toHistoryOrderInfo: response.data.toHistoryOrderInfo,
            commissionHistoryOrderInfo: response.data.commissionHistoryOrderInfo
          })
        }
      })

    axios.get(config.baseurl + '/accounts/fetchAllOpenTrade')
      .then(function (response) {
        if (response.status == 200) {
          self.setState({
            allOpenRecord: response.data.rows,
            fromOpenOrderInfo: response.data.fromOpenOrderInfo,
            toOpenOrderInfo: response.data.toOpenOrderInfo
          })
        }
      })
  }
  createLegend(json) {
    var legend = [];
    for (var i = 0; i < json["names"].length; i++) {
      var type = "fa fa-circle text-" + json["types"][i];
      legend.push(<i className={type} key={i} />);
      legend.push(" ");
      legend.push(json["names"][i]);
    }
    return legend;
  }

  getAllAccounts = () => {
    let self = this;
    axios.get(config.baseurl + '/accounts/fetchAllAccounts')
      .then(function (response) {

        if (response.status == 200) {
          self.setState({ accountlist: response.data.rows })
        }
      });
  }

  fetchActivefilterData = () => {
    let self = this;
    axios.get(config.baseurl + '/filter/fetchActivefilterdata')
      .then(function (response) {
        if (response.status == 200) {

          self.setState({
            filterlist: response.data.rows,

          })
          if (self.state.filterlist.length == 0) {

            self.setState({ errorMassege: true })
          }
          else {
            self.setState({

              activeProfileName: response.data.rows[0].profile_name,
              autoClose: response.data.rows[0].auto_close !== 0 ? 'ON' : 'OFF'
            })
          }
        }
      });
  }

  fetchAllOpenTrade = () => {
    let self = this;
    axios.get(config.baseurl + '/accounts/fetchLastUpdatedTime')
      .then(function (response) {
        if (response.status == 200) {
          self.setState({ dateTime: response.data.filteredInfoTime })
        }
      });
  }

  fetchStatusData = () => {
    let self = this;
    let tottal = 0;
    axios.get(config.baseurl + '/accounts/fetchStatusData')
      .then(function (response) {
        console.log(response, "active dat")

        if (response.status == 200) {
          self.setState({ loading: false })

          self.setState({ fromAccounts: response.data.fromAccounts })
          self.setState({ fromSymbol: response.data.fromSymbol })
          self.setState({ toAccounts: response.data.toAccounts })
          self.setState({ toSymbol: response.data.toSymbol })
          self.setState({ fromOpenOrderInfos: response.data.fromOpenOrderInfos[0] })
          if (response.data.fromOpenOrderInfos.length > 0) {
            self.setState({ blockloader: false })
            if (response.data.fromOpenOrderInfos[0].order_type == 0) {
              self.setState({ total: response.data.fromSymbol.contract_size * (response.data.fromOpenOrderInfos[0].lots) })
            } else {
              self.setState({ total: response.data.fromSymbol.contract_size * -(response.data.fromOpenOrderInfos[0].lots) })
            }
            console.log(response.data.fromSymbol.contract_size * -(response.data.fromOpenOrderInfos[0].lots), "total");
            self.setState({ toOpenOrderInfos: response.data.toOpenOrderInfos[0] })
          }
          if (response.data.toOpenOrderInfos.length > 0) {
            if (response.data.toOpenOrderInfos[0].order_type == 0) {
              self.setState({ totalTo: response.data.toSymbol.contract_size * (response.data.toOpenOrderInfos[0].lots) })
            } else {
              self.setState({ totalTo: response.data.toSymbol.contract_size * -(response.data.toOpenOrderInfos[0].lots) })
            }
          }
        }
      });
  }




  showData = (accountDetailValue) => {

    return this.state.filterlist.length > 0 && this.state.filterlist.map((item, index) => {
      if (accountDetailValue === 'margin_level') {
        return (
          <>
            <td className="tdClr2 tdClr boldText" key={index}>
              <NumberFormat value={item.accountFromInfo[0].accounts_details[0][accountDetailValue]} displayType={'text'} thousandSeparator={true} decimalScale={2} /> %
            </td>
            <td className="tdClr2 boldText" key={index}>
              <NumberFormat value={item.accountToInfo[0].accounts_details[0][accountDetailValue]} displayType={'text'} thousandSeparator={true} decimalScale={2} /> %
            </td>
          </>
        )
      }
      if (item.accountFromInfo.length > 0) {
        return (
          <>
            <td className="tdClr2 tdClr boldText" key={index}>
              {item.accountFromInfo[0].accounts_details[0].currency == 'USD' ? '$' : ''}  {' '}
              <NumberFormat value={item.accountFromInfo[0].accounts_details[0][accountDetailValue]} displayType={'text'} thousandSeparator={true} decimalScale={2} />
            </td>

            <td className="tdClr2 boldText" key={index}>
              {item.accountToInfo[0].accounts_details[0].currency == 'USD' ? '$' : ''} {' '}
              <NumberFormat value={item.accountToInfo[0].accounts_details[0][accountDetailValue]} displayType={'text'} thousandSeparator={true} decimalScale={2} />

            </td>
          </>
        )

      }

    })
  }

  tableHeader = () => {

    return this.state.filterlist.length > 0 && this.state.filterlist.map((item, index) => {
      return (
        <>
          <th key={index} className="bText colorDarkBlue text-center tdClr2 tdClr">
            <div className="colorDarkBlue" >
              {(item.accountFromInfo[0].alias != null) ? item.accountFromInfo[0].alias : '-'}
            </div>
            <span style={{ fontSize: 10 }}>
              {item.accountFromInfo[0].login}
            </span>

          </th>
          <th key={index} className="bText color2 text-center tdClr2">
            <div className="color2" >
              {(item.accountToInfo[0].alias != null) ? item.accountToInfo[0].alias : '-'}
            </div>
            <span style={{ fontSize: 10 }}>
              {item.accountToInfo[0].login}
            </span>

          </th>
        </>

      )

    })
  }





  render() {
    const { allHistoryRecord, fromHistoryOrderInfo, toHistoryOrderInfo, commissionHistoryOrderInfo,
      allOpenRecord, fromOpenOrderInfo, toOpenOrderInfo, } = this.state;


    let fromsum = 0;
    let tosum = 0;
    let fromsum1 = 0;
    let tosum1 = 0;


    if (fromHistoryOrderInfo && fromHistoryOrderInfo.length > 0) {
      fromsum = (fromHistoryOrderInfo[0].commission + fromHistoryOrderInfo[0].swap + fromHistoryOrderInfo[0].profit);
    }
    if (toHistoryOrderInfo && toHistoryOrderInfo.length > 0) {
      tosum = (toHistoryOrderInfo[0].commission + toHistoryOrderInfo[0].swap + toHistoryOrderInfo[0].profit);
    }

    let sumOfTotal = tosum + fromsum;
    if (fromOpenOrderInfo && fromOpenOrderInfo.length > 0) {

      fromsum1 = (fromOpenOrderInfo[0].commission + fromOpenOrderInfo[0].swap + fromOpenOrderInfo[0].profit);

    }
    if (toOpenOrderInfo && toOpenOrderInfo.length > 0) {

      tosum1 = (toOpenOrderInfo[0].commission + toOpenOrderInfo[0].swap + toOpenOrderInfo[0].profit);

    }

    let sumOfTotal1 = tosum1 + fromsum1;
    const dateToFormat = new Date();
    let FinalTotal = 0

    return (

      <div className="">
        {this.state.errorMassege == true &&
          <div className="popup">

            <div className="card w-75 ">
              <div className="card-body topmargin">
                <div className=" card-text popuptext" role="alert">Please Select Filters To Continue...</div>
              </div>
            </div>
          </div>}
        <div className="content">
          {this.state.errorMassege != true &&

            <Grid fluid>

              <Row>
                <div className="lastUpdate">
                  <span> Last Updated :  </span>
                  {this.state.dateTime}(UTC)  </div>
                <Col md={12} sm={6}>
                  <div className="terminal-box  terminal-head-box">

                    <span className="terminal-text">Profile</span>
                  </div>
                  <Row >
                    <Col lg={4} sm={4}>
                      <div className="card-margin card card-stats">
                        <div className="content">
                          <Row className="row">
                            <Col xs={5}>
                              <div style={{ fontSize: '3em' }} className="icon-big text-center icon-warning">
                                <i className="pe-7s-display1 text-warning"></i>
                              </div>
                            </Col>
                            <div className="col-xs-7">
                              <div className="numbers"><p style={{
                                textAlign: 'center',
                                fontSize: '17px',
                                marginTop: '15px'
                              }}>Active Profile </p></div>
                            </div>
                          </Row>
                          <div className="footer"><hr /><p style={{ textAlign: 'center', fontSize: '17px' }}>{this.state.activeProfileName}</p></div></div></div>
                    </Col>

                    <div className=" col-lg-offset-4 col-sm-offset-4 col-lg-4 col-sm-4">
                      <div className="card-margin card card-stats">
                        <div className="content">
                          <div className="row">
                            <div className="col-xs-5">
                              <div style={{ fontSize: '3em' }} className="icon-big text-center icon-warning">
                                <i style={{ color: this.state.autoClose == 'OFF' ? '#312f2f' : 'green' }} className="pe-7s-switch text-warning-switch"></i>
                              </div></div>
                            <div className="col-xs-7">
                              <div className="numbers"><p style={{
                                textAlign: 'center',
                                fontSize: '17px',
                                marginTop: '15px'
                              }}>Auto-Close </p></div>
                            </div></div><div className="footer"><hr /><p style={{ textAlign: 'center', fontSize: '17px' }}>{this.state.autoClose}</p></div></div></div></div>

                  </Row>
                  <div className="terminal-box terminal-head-box">
                    {/* {(this.state.errorMassege) && <p style={{ color: 'red', fontSize: '10px' }}>Please Select Any Account</p>} */}
                    <span className="terminal-text">Profit/Loss</span>
                  </div>
                  <Row >
                    <div className="col-lg-12 col-sm-12">
                      <div className="card-margin card card-stats">
                        <div className="content">
                          <div className="row">
                            <div className="col-xs-6">
                              <div className="numbers"><p style={{
                                textAlign: 'center',
                                fontSize: '20px',
                        
                                color: sumOfTotal > 0 ? 'green' : 'red'
                              }}>Realized : $ {sumOfTotal} </p></div>
                            </div>
                            <div className="col-xs-6">
                              <div className="numbers"><p style={{
                                textAlign: 'center',
                                fontSize: '20px',
                            
                                color: sumOfTotal1 > 0 ? 'green' : 'red'

                              }}>Unrealized : $ {sumOfTotal1} </p></div>
                            </div>
                          </div><div className="footer"><hr /><p style={{ color: (sumOfTotal1 + sumOfTotal) > 0 ? 'green' : 'red', textAlign: 'center', fontSize: '20px' }}>Total : $ {(sumOfTotal1 + sumOfTotal).toFixed(2)}</p></div></div></div></div>
                  </Row>
                  <div className="terminal-box  terminal-head-box">
                    {/* {(this.state.errorMassege) && <p style={{ color: 'red', fontSize: '10px' }}>Please Select Any Account</p>} */}
                    <span className="terminal-text">Exposure</span>
                  </div>
                  {this.state.loading ? <Loader
                    type="ThreeDots"
                    color="black"
                    height={100}
                    width={100}
                    style={{ textAlign: 'center' }}


                  />
                    :
                    <Row>
                      {this.state.fromOpenOrderInfos &&
                        // this.state.fromOpenOrderInfos.order_type !== null &&
                        <div className="col-sm-6">
                          <div className="c-callout c-callout-info">
                            <small className="text-muted text-muted-dashboard">{this.state.fromOpenOrderInfos.order_type == 0 ? 'long ' : 'short '} </small>
                            <br />
                            <strong className="h4 buy-sell-h4"><NumberFormat value={this.state.total} displayType={'text'} thousandSeparator={true} decimalScale={2} />   {this.state.fromSymbol.margin_currency}
                              <br />  {this.state.fromOpenOrderInfos.lots} {(this.state.fromOpenOrderInfos.lots > 1) ? 'Lots' : 'Lot'}</strong>
                          </div>
                        </div>}
                      {this.state.toOpenOrderInfos &&
                        // this.state.toOpenOrderInfos.order_type !== null &&
                        <div className="col-sm-6">
                          <div className="c-callout callout-danger">
                            <small className="text-muted text-muted-dashboard">{this.state.toOpenOrderInfos.order_type == 0 ? 'long ' : 'short '} </small>
                            <br />
                            <strong className="h4 buy-sell-h4"><NumberFormat value={this.state.totalTo} displayType={'text'} thousandSeparator={true} decimalScale={2} />   {this.state.toSymbol.margin_currency}
                              <br /> {this.state.toOpenOrderInfos.lots}  {(this.state.toOpenOrderInfos.lots > 1) ? 'Lots' : 'Lot'} </strong>
                          </div></div>}
                    </Row>
                  }
                </Col>
              </Row>
              <Row style={{ margin: '20px 0px' }}></Row>
            </Grid>
          }
        </div>
      </div>
    );
  }
}

export default Dashboard;
